@use "../mixins.scss" as *;

.inner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    input,
    select {
        @include inputStyle;
    }
    button {
        @include pinkButton(140px, 40px);
        align-self: center;
    }
}
